/* Basic re-flex panel/splitter styling */
.reflex-container {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  /* align items in Main Axis */
  -webkit-align-items: stretch;
          align-items: stretch;
  /* align items in Cross Axis */
  -webkit-align-content: stretch;
          align-content: stretch;
  /* OLD - iOS 6-, Safari 3.1-6 */
  /* OLD - Firefox 19- (buggy but mostly works) */
  /* TWEENER - IE 10 */
  /* NEW - Chrome */
  display: -webkit-flex;
  display: flex;
  position: relative;
  height: 100%;
  width: 100%; }

.reflex-container.horizontal {
  -webkit-flex-direction: column;
          flex-direction: column; }

.reflex-container.vertical {
  -webkit-flex-direction: row;
          flex-direction: row; }

.reflex-container > .reflex-element {
  position: relative;
  overflow: auto;
  height: 100%;
  width: 100%; }

.reflex-container.reflex-resizing > .reflex-element {
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.reflex-container > .reflex-element > .reflex-size-aware {
  height: 100%;
  width: 100%; }

.reflex-container > .reflex-splitter {
  background-color: #191919;
  z-index: 0; }

.reflex-container > .reflex-splitter.active,
.reflex-container > .reflex-splitter:hover {
  background-color: #AFAFAF;
  -webkit-transition: all 1s ease;
